body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 10vh;
    width: 7vw;
}

.App-header {
    background-color: #282c34;
    min-height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    margin-bottom: 50px;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.Button {
    background: #282c34;
    display: flex;
    box-sizing: border-box;
    border: 1px solid whitesmoke;
    align-items: center;
    justify-content: center;
    width: 20vw;
    font-size: 24px;
    color: white;
    height: 5vh;
    margin: 30px auto;
}

.Button:hover {
    background: #d7ddd3;
    color: #282c34;
    cursor: pointer;
}

.Button:active {
    background: #200020;
    color: white;
}

.Movies,
.Series {
    display: flex;
    margin: 0 auto;
    box-sizing: border-box;
    border: 1px solid black;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    width: 10vw;
    height: 5vh;
    color: black;
}

.Movies:hover,
.Series:hover {
    background-color: #59C3C3;
    color: white;
}

.Series {
    background-color: black;
    color: white;
}
.Input {
    display: flex;
    box-sizing: border-box;
    border: 1px solid #420062;
    height: 5vh;
    width: 30vw;
    font-size: 1.5em;
    padding: 10px;
    margin: 30px auto;
}
.Results{
    display: flex;
    width: 100vw;
    justify-content: center;
    flex-wrap: wrap;

}
.Thumbnail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 40vh;
    width: 20vw;
    margin: 5px;
    border: 1px solid darkgray;
}

.Thumbnail:hover {
    scale: 2;
}

h1 {
    font-size: 1.3em;
    margin: 0 10%;
    height: 10vh;
}

img {
    display: flex;
    margin: 0 auto;
    height: 20vh;
    width: 10vw;
}
